body {
  text-align: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* ----------------------------------------------- navbar */
.Navbar {
  position: absolute;
  width: 100vw;
  z-index: 1050;
}

.navLogoAndTitle {
  cursor: pointer;
}
.buttonHovered {
  position: absolute;
}
.navButtonsContainer {
  float: right;
  margin: 30px;
  width: 50%;
}
.buttonContainer {
  float: left;
  width: 20%;
  height: 50px;
}
.navButton {
  background: transparent;
  border: 0;
  outline: none;
  transition: 0.5s;
  opacity: 0.5;
  letter-spacing: 2px;
  cursor: pointer;
}
.navButton:hover {
  opacity: 1;
}
.navLogo {
  height: 100px;
  float: left;
  position: relative;
  left: 20px;
}
.navBanner {
  background: white;
  width: 100%;
  height: 100px;
  text-align: center;
}
.navTitle {
  float: left;
  font-size: 20px;
  position: relative;
  width: 25%;
}

.navbarSpacer {
  z-index: -100;
  height: 100px;
}
.navTitleStorypoint {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 30px;
}
.navTitleSubtext {
  font-size: 15px;
  opacity: 0.5;
}
/* ----------------------------------------------- footer */
.Footer {
  text-align: center;
  height: 375px;
  background: rgb(223, 223, 223);
}
.footerAddresses {
  width: 70%;
  margin-left: 15%;
}
.address {
  text-align: center;
  width: 50%;
  float: left;
}
.footerWhiteSpace {
  height: 100px;
}
.social-icon {
  margin-left: 5px;
  margin-right: 5px;
}
.footerAddressType {
  text-decoration: underline;
  margin-bottom: 0;
}
.footerAddress {
  margin-top: 10px;
}
/* ----------------------------------------------- content */

.centeredh3 {
  text-align: center;
  text-transform: uppercase;
}
.content {
  text-align: center;
  width: 80%;
  margin-left: 10%;
}
.whiteSpace {
  height: 100px;
  background: white;
}
.transparentSpace {
  height: 300px;
}
/* ----------------------------------------------------------parallax */
.parallaxSizePrimary {
  height: 80vh;
}
.parallaxSizeSecondary {
  height: 100vh;
}
.parallaxContainer {
  margin-bottom: 100px;
}
.parallaxContent {
  text-align: center;
}
.parallaxTitleContainer {
  text-align: center;
  position: absolute;
  width: 100%;
  top: 40%;
  text-align: center;
}
.parallaxTitle {
  padding-left: 20px;
  letter-spacing: 20px;
  /* padding: 20px; */
  width: fit-content;
  margin: auto;
  color: #fff;
  font-size: 80px;
}
.parallaxWhiteSpaceTop {
  height: 100px;
}
/* ---------------------------------------------------------- beliefs */
.ourBeliefs {
  text-align: left;
}
.scripture {
  margin-top: 0;
  opacity: 0.5;
}
.valuesParagraph {
  text-align: center;
}
/* ---------------------------------------------------------- contact */
.allContactsContainer {
  text-align: center;
}
.contactContainer {
  display: inline-block;
  width: 80vw;
  height: 250px;
  background: whitesmoke;
  margin: 10px;
  border-style: solid;
  border-width: 5px;
  border-color: rgba(128, 128, 128, 0.459);
  /* border-radius: 10px; */
}
.contactContainer:hover {
  background: rgb(228, 226, 226);
  border-color: gray;
}

.contactImg {
  height: 90%;
  margin: 10px;
  float: left;
}
.contactName {
  padding-top: 30px;
}
.contactBio {
  height: 70%;
  width: 100%;
}

@media only screen and (max-width: 600px) {
  /* --------------------------------------navbar------------------------------------------ */
  body {
    width: 100%;
  }
  .Navbar {
    position: absolute;
    width: 100vw;
    z-index: 1050;
  }
  .navBanner {
    background: white;
    width: 100%;
    height: 100px;
    text-align: center;
    transition: 0.5s;
  }
  .navLogoAndTitle {
    width: 100%;
    height: 100px;
    cursor: auto;
  }
  .navLogo {
    max-width: 45%;
    height: 100px;
    float: left;
    position: relative;
    left: 0px;
    cursor: pointer;
  }
  .navTitle {
    position: absolute;
    float: left;
    font-size: 20px;
    width: 50%;
    margin-left: 25%;
  }
  .menuBarContainer {
    margin-top: 25px;
    float: right;
    width: 15%;
    cursor: pointer;
  }

  .buttonHovered {
    position: absolute;
  }
  .navButtonsContainer {
    float: left;
    margin: 0px;
    width: 100%;
    transition: 0.5s;
  }
  .buttonContainer {
    float: left;
    width: 20%;
    height: 50px;
  }
  .navButton {
    background: transparent;
    font-size: 15px;
    margin-bottom: 25px;
    border: 0;
    outline: none;
    transition: 0.5s;
    opacity: 0.5;
    letter-spacing: 0px;
    cursor: pointer;
  }
  .navButton:hover {
    opacity: 1;
  }

  .navbarSpacer {
    z-index: -100;
    height: 100px;
  }
  .navTitleStorypoint {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 35px;
  }
  .navTitleSubtext {
    font-size: 15px;
    opacity: 0.5;
  }
  .bar1,
  .bar2,
  .bar3 {
    width: 35px;
    height: 5px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.5s;
  }
  .bar1change {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  .bar2change {
    opacity: 0;
  }

  .bar3change {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }

  /* --------------------------------------site settings------------------------------------------ */
  .content {
    text-align: center;
    width: 96%;
    margin-left: 2%;
  }
  .parallaxTitle {
    font-size: 45px;
  }
  .parallaxContainer {
    margin-bottom: 40px;
  }
  .parallaxWhiteSpaceTop {
    height: 40px;
  }
  .footerWhiteSpace {
    height: 40px;
  }
  .Footer {
    height: 420px;
  }
  /* --------------------------------------home------------------------------------------ */

  .App {
    width: 100vw;
  }
  /* --------------------------------------about------------------------------------------ */

  .youtubeWindow {
    width: 100%;
    height: 300px;
  }

    /* --------------------------------------events------------------------------------------ */

    .facebookWindow {
      width: 100%;
    }
}
